.glow-on-hover {
  z-index: 0;
  background: #000;
  border: none;
  border-radius: 10px;
  outline: none;
  position: relative;
}

.glow-on-hover:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  opacity: 1;
  background: linear-gradient(90deg, #fff, #ff6b09, #fff, #ff6b09, #fff, #ff6b09, #fff) 0 0 / 400%;
  border-radius: 10px;
  transition: opacity .3s ease-in-out;
  animation: glowing 30s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

.glow-on-hover:hover {
  color: #000;
}

.glow-on-hover:hover:after {
  background: none;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #ff6b09;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

